import "./styles.css";
import { React, useState } from "react";
export default function App() {
  const [data, setData] = useState("");
  const [list, setList] = useState([]);
  const handler = (e) => {
    setData(e.target.value);
  };
  const submitHandler = (e) => {
    e.preventDefault();
    const newTodos = [...list, data];
    setList(newTodos);
    setData("");
  };
  const deleteHandler = (idxValue) => {
    const newTodos = list.filter((list, index) => idxValue !== index);
    setList(newTodos);
  };
  return (
    <div className="App">
      <h1>Task Managing Application</h1>
      <form onSubmit={submitHandler} autoComplete="off">
        <input
          type="text"
          onChange={handler}
          name="data"
          value={data}
          required
          placeholder="Task Name"
        />
        <button type="submit">Add </button>
      </form>
      <h1>TO-DO List </h1>
      {list.map((li, index) => (
        <div key={index} className="ToDo">
          <h3>
            {index + 1}) {li}
          </h3>
          <button onClick={() => deleteHandler(index)}>Delete</button>
        </div>
      ))}
    </div>
  );
}
